* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 16px;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
}

body {
  font-family: "Georgia", serif;
  line-height: 1.6;
  color: #333;
  background-color: #f8f8f8;
  overflow-x: hidden;
  width: 100%;
  min-width: 320px;
}

/* Header and Navigation */
header {
  background: linear-gradient(135deg, #2c5530, #3d7c47);
  padding: 1rem 0;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

nav {
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  gap: 2rem;
  padding: 0 2rem;
}

.tab-button {
  background: none;
  border: 2px solid #fff;
  color: #fff;
  padding: 0.75rem 2rem;
  font-size: 1.1rem;
  font-family: inherit;
  cursor: pointer;
  border-radius: 25px;
  transition: all 0.3s ease;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.tab-button:hover {
  background-color: #fff;
  color: #2c5530;
  transform: translateY(-2px);
}

.tab-button.active {
  background-color: #fff;
  color: #2c5530;
}

/* Content Container */
#content {
  max-width: 1200px;
  margin: 0 auto;
  padding: 1rem;
  min-height: calc(100vh - 100px);
  width: 100%;
}

.tab-content {
  animation: fadeIn 0.5s ease-in-out;
  width: 100%;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Home Page Styles */
.hero {
  text-align: center;
  margin-bottom: 3rem;
  padding: 3rem 0;
  background: white;
  border-radius: 15px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
}

.hero h1 {
  font-size: 3.5rem;
  color: #2c5530;
  margin-bottom: 1rem;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.1);
}

.hero h2 {
  font-size: 1.5rem;
  color: #666;
  margin-bottom: 2rem;
  font-style: italic;
}

.hero p {
  font-size: 1.2rem;
  max-width: 800px;
  margin: 0 auto 2rem;
  color: #555;
}

.restaurant-image {
  background: linear-gradient(45deg, #ff6b6b, #feca57);
  color: white;
  padding: 4rem 2rem;
  margin: 2rem auto;
  border-radius: 15px;
  font-size: 2rem;
  max-width: 600px;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
}

.features {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
  margin-top: 3rem;
}

.feature {
  background: white;
  padding: 2rem;
  border-radius: 15px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  text-align: center;
  transition: transform 0.3s ease;
}

.feature:hover {
  transform: translateY(-5px);
}

.feature h3 {
  color: #2c5530;
  margin-bottom: 1rem;
  font-size: 1.5rem;
}

/* Menu Page Styles */
.menu-section {
  background: white;
  margin-bottom: 2rem;
  padding: 2rem;
  border-radius: 15px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
}

.menu-section h2 {
  color: #2c5530;
  font-size: 2rem;
  margin-bottom: 1.5rem;
  border-bottom: 3px solid #3d7c47;
  padding-bottom: 0.5rem;
}

.menu-item {
  margin-bottom: 1.5rem;
  padding-bottom: 1rem;
  border-bottom: 1px solid #eee;
}

.menu-item:last-child {
  border-bottom: none;
}

.menu-item-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 0.5rem;
}

.menu-item-name {
  font-weight: bold;
  font-size: 1.2rem;
  color: #2c5530;
}

.menu-item-price {
  font-weight: bold;
  color: #ff6b6b;
  font-size: 1.1rem;
}

.menu-item-description {
  color: #666;
  font-style: italic;
}

/* Contact Page Styles */
.contact-info {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 2rem;
  margin-bottom: 3rem;
}

.contact-section {
  background: white;
  padding: 2rem;
  border-radius: 15px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  text-align: center;
}

.contact-section h3 {
  color: #2c5530;
  font-size: 1.5rem;
  margin-bottom: 1rem;
}

.reservation-section {
  background: white;
  padding: 2rem;
  border-radius: 15px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  margin-bottom: 2rem;
}

.reservation-section h2 {
  color: #2c5530;
  font-size: 2rem;
  margin-bottom: 1rem;
}

.reservation-note {
  background: #f0f8f0;
  padding: 1.5rem;
  border-radius: 10px;
  margin-top: 2rem;
  border-left: 4px solid #3d7c47;
}

.reservation-note h4 {
  color: #2c5530;
  margin-bottom: 0.5rem;
}

.map-section {
  background: white;
  padding: 2rem;
  border-radius: 15px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  text-align: center;
}

.map-section h3 {
  color: #2c5530;
  font-size: 1.5rem;
  margin-bottom: 1rem;
}

.map-placeholder {
  background: linear-gradient(45deg, #74b9ff, #0984e3);
  color: white;
  padding: 3rem;
  border-radius: 10px;
  font-size: 1.5rem;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
}

/* Responsive Design */
@media screen and (max-width: 768px) {
  html {
    font-size: 14px;
  }

  body {
    font-size: 1rem;
  }

  header {
    padding: 0.5rem 0;
  }

  nav {
    flex-direction: column;
    gap: 0.5rem;
    padding: 0 1rem;
  }

  .tab-button {
    padding: 0.5rem 1rem;
    font-size: 1rem;
    width: 100%;
    margin: 0.25rem 0;
  }

  #content {
    padding: 1rem 0.5rem;
  }

  .hero {
    padding: 2rem 1rem;
    margin-bottom: 2rem;
  }

  .hero h1 {
    font-size: 2rem;
    line-height: 1.2;
  }

  .hero h2 {
    font-size: 1.2rem;
  }

  .hero p {
    font-size: 1rem;
  }

  .restaurant-image {
    padding: 2rem 1rem;
    font-size: 1.5rem;
    margin: 1rem 0;
  }

  .features {
    grid-template-columns: 1fr;
    gap: 1rem;
  }

  .feature {
    padding: 1.5rem 1rem;
  }

  .menu-section,
  .contact-section,
  .reservation-section,
  .map-section {
    padding: 1.5rem 1rem;
    margin-bottom: 1rem;
  }

  .menu-section h2 {
    font-size: 1.5rem;
  }

  .menu-item-header {
    flex-direction: column;
    align-items: flex-start;
    gap: 0.25rem;
  }

  .menu-item-name {
    font-size: 1.1rem;
  }

  .contact-info {
    grid-template-columns: 1fr;
    gap: 1rem;
  }

  .map-placeholder {
    padding: 2rem 1rem;
    font-size: 1.2rem;
  }
}

@media screen and (max-width: 480px) {
  html {
    font-size: 13px;
  }

  .hero h1 {
    font-size: 1.5rem;
  }

  .hero h2 {
    font-size: 1rem;
  }

  .restaurant-image {
    font-size: 1.2rem;
    padding: 1.5rem 0.5rem;
  }

  .menu-section,
  .contact-section,
  .reservation-section,
  .map-section {
    padding: 1rem 0.5rem;
  }

  .feature {
    padding: 1rem 0.5rem;
  }
}
